import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { ShoppingListPage } from '../pages/shopping-list/shopping-list';
import { AddShoppingPage } from '../pages/add-shopping/add-shopping';
import { EditShoppingItemPage} from '../pages/edit-shopping-item/edit-shopping-item';

// Import the AF2 Module
import { AngularFireModule } from 'angularfire2';
import{ AngularFireDatabaseModule} from 'angularfire2/database';
import { FIREBASE_CREDENTIALS } from './firebase.credentials';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { DataServiceProvider } from '../providers/data-service/data-service';


@NgModule({
  declarations: [
    MyApp,
    EditShoppingItemPage, AddShoppingPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-shopping/add-shopping.module#AddShoppingPageModule', name: 'AddShoppingPage', segment: 'add-shopping', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-shopping-item/edit-shopping-item.module#EditShoppingItemPageModule', name: 'EditShoppingItemPage', segment: 'edit-shopping-item', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/shopping-list/shopping-list.module#ShoppingListPageModule', name: 'ShoppingListPage', segment: 'shopping-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/add-item-to-master-list/add-item-to-master-list.module#AddItemToMasterListPageModule', name: 'AddItemToMasterListPage', segment: 'add-item-to-master-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/add-to-my-next-trip/add-to-my-next-trip.module#AddToMyNextTripPageModule', name: 'AddToMyNextTripPage', segment: 'add-to-my-next-trip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-profile/edit-profile.module#EditProfilePageModule', name: 'EditProfilePage', segment: 'edit-profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-of-lists/list-of-lists.module#ListOfListsPageModule', name: 'ListOfListsPage', segment: 'list-of-lists', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/master-list/master-list.module#MasterListPageModule', name: 'MasterListPage', segment: 'master-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-next-trip/my-next-trip.module#MyNextTripPageModule', name: 'MyNextTripPage', segment: 'my-next-trip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-next-trip-shopping-list-for-store/my-next-trip-shopping-list-for-store.module#MyNextTripShoppingListForStorePageModule', name: 'MyNextTripShoppingListForStorePage', segment: 'my-next-trip-shopping-list-for-store', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/shopping-mode/shopping-mode.module#ShoppingModePageModule', name: 'ShoppingModePage', segment: 'shopping-mode', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs-home/tabs-home.module#TabsHomePageModule', name: 'TabsHomePage', segment: 'tabs-home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user-profile/user-profile.module#UserProfilePageModule', name: 'UserProfilePage', segment: 'user-profile', priority: 'low', defaultHistory: [] }
  ]
}),
    //initialize angularfire with credentials 
    AngularFireModule.initializeApp(FIREBASE_CREDENTIALS),
    AngularFireDatabaseModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp, EditShoppingItemPage, AddShoppingPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AngularFireAuth,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthServiceProvider,
    DataServiceProvider
  ]
})
export class AppModule {}



